.skill-set-head{
    font-size: 2rem;
    font-weight: 700;
    position: relative;
    bottom: 8vh;
    color: #563D7C;
}
.skill-description{
    text-align: center;
}
.skills-shadow{
    font-size: 3.5rem;
    font-weight: 700;
    color: rgba(255, 255, 255, 0.027);
}
.skill-inner{
    font-size: 18px;
    line-height: 1.8rem;
    font-weight: 400;
}
.skills h2{
    font-size: 24px;
    font-weight: 700;
}
.skills p{
    font-size:16px ;
    font-weight: 400;
    color: #8491A0;
    line-height: 1.5rem;
}
.logos{
    border: 1px solid #333333;
}
.html a{
    color: #FD7B1C;
    text-decoration: none;
    transition: 0.8s;
}
.html a:hover{
    color: #ffffff;
    filter: drop-shadow(0rem 0.25rem 0.2rem #FD7B1C);
}
.css a{
    color: #2195F2;
    text-decoration: none;
    transition: 0.8s;
}
.css a:hover{
    color: #ffffff;
    filter: drop-shadow(0rem 0.25rem 0.2rem #2195F2);
}
.bootstrap a{
    color: #563D7C;
    text-decoration: none;
    transition: 0.8s;
}
.bootstrap a:hover{
    color: #ffffff;
    filter: drop-shadow(0rem 0.25rem 0.2rem #563D7C);
}
.javascript a{
    color: #F6DE1E;
    text-decoration: none;
    transition: 0.8s;
}
.javascript a:hover{
    color: #ffffff;
    filter: drop-shadow(0rem 0.25rem 0.2rem #F6DE1E);
}
.react a{
    color: #2384CF;
    text-decoration: none;
    transition: 0.8s;
}
.react a:hover{
    color: #ffffff;
    filter: drop-shadow(0rem 0.25rem 0.2rem #2384CF);
}

.Python a{
    text-decoration: none;
    transition: 0.8s;
    color: #FEEF55;
}
.Python a:hover{
    color:  #ffffff;
    filter: drop-shadow(0rem 0.25rem 0.2rem #FEEF55);
}
@media only screen and (min-width:2560px) {

    /* Section 4 */
    .skill-set-head{
        font-size: 8rem;
    }
    .skill-inner{
        font-size: 30px;
        line-height: 2.5rem;
    }
    .skills h2{
        font-size: 40px;
    }
    .skills p{
        font-size:28px ;
        line-height: 2.5rem;
    }
  
  }


  @media only screen and (max-width:426px){
    .skill-set-head{
        position: relative;
        bottom: 9vh;
    }
    .skill-description{
        text-align: start;
        text-indent: 3rem;
    }
  }
    

  
  