/* Background Style */
.bg-colors{
    height: 100vh;
}
.bg-colors div:nth-child(1){
    background-color: black;
}
.bg-colors div:nth-child(2){
    background-color: rgb(24, 24, 35);
}

.customIntroColor{
    color: #602a68;
}

/* Main Style */
h1 strong{
  
    font-size: 74px
}
h1{
    font-size: 74px
}
.profile-img{
    max-width: 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.main{
    position: absolute;
    top: 0;
    height: 100%;
    display:flex ;
    align-items: center;
}
.hero-details{
    position: relative;
    left: -6vw;
}
@media only screen and (min-width:2560px) {

    /* Section 1 */
   
    .hero-details{
        position: relative;
        left: -5vw;
    }
    .hero-details h1{
        font-size: 86px;
    }
    .hero-details h1 strong{
        font-size: 86px;
    }
    .hero-detail{
        font-size: 26px;
    }
    /* Section 2 */
  
  }
    
  /* Laptop screen */
  @media only screen and (max-width:1440px)  {
    .hero-details{
        position: relative;
        left: -7vw;
    }
    .hero-details  h1{
        font-size: 70px;
    }
    .hero-details h1 strong{
        font-size: 70px;
    }
    .hero-detail{
        font-size: 16px;
    }
  }
  
  /* Mini Laptop Screen */
  @media only screen and (max-width:1024px)  {
    .hero-details h1{
        font-size: 48px;
    }
    .hero-details   h1 strong{
        font-size: 48px;
    }
    .hero-detail{
        font-size: 12px;
    }

  }
  
  /* Tablet and mobile */
  @media only screen and (max-width:768px)  {
    .intro-section{
        padding: 20px;
    }
    .hero-details{
        position: relative;
        left: 0;
    }
    .hero-details h1{
        padding-top: 2vh;
        font-size: 40px;
    }
   .hero-details h1 strong{
        font-size: 40px;
    }
    .hero-detail{
        font-size: 15px;
        padding-top: 1vh;
    }
   
  }
  
