.SocialMedia{
    max-width: 100%;
}
.social-content{
    background-color: black;
}
.social-content h2{
    font-size: 1.5rem;
    font-weight: 700;
    text-align: center;
    color: #EBA371;
    position: relative;
    bottom: 6vh;
    letter-spacing: 5PX;
}
.social-content h3{
    font-size: 3.5rem;
    font-weight: 700;
    text-align: center;
    color: #f6caad09;
}
.social-content p{
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
    color: white;
    text-align: center;
}
@media only screen and (max-width:426px){
    .social-content p{
        text-indent: 35px;
        text-align: justify;
        font-size: 15px;
    }
  
}

