@import url('https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap');

.resume-content{
    color: #8B887C;
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
    text-align: center;
}
.animation{
  color: grey;
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

.download {
    font-size: 25px;
    font-family: 'Shadows Into Light', cursive;
    color: rgb(33,150,243);
    font-weight: 700;
    text-align: center;
    letter-spacing: 3px;
}
.download a{
    text-decoration: none;
}
.resume-shadow{
  text-align: center;
  color: rgba(33, 149, 243, 0.041);
  font-size: 3.5rem;
  font-weight: 700;
}
.resume-head{
  text-align: center;
  color: white;
  font-size: 1.5rem;
  position: relative;
  bottom: 6vh;
  letter-spacing: 5px;
  font-weight: 700;
  color: rgb(33,150,243);
}

.resume-image img{
  max-width: 100%;

}
.resume-image{
  display: contents;
}

@-webkit-keyframes up-down {
    0% {
      transform: translateY(10px);
    }
    100% {
      transform: translateY(-10px);
  
    }
  }
  
  @keyframes up-down {
    0% {
      transform: translateY(10px);
      filter: drop-shadow(0rem 0.1rem 0.1rem #7a7b7c80);
    }
    100% {
      transform: translateY(-10px); 
    }
  }



@media only screen and (max-width:426px){
  .resume-content{
    text-align: justify;
    font-size: 15px;
  }

}