    font-weight: 700;
}
.skills p{
    font-size:16px ;
    font-weight: 400;
    color: #8491A0;
    line-height: 1.5rem;
}
.logos{
    border: 1px solid #333333;
}
.html a{
    color: #FD7B1C;
    text-decoration: none;
    transition: 0.8s;
}
.html a:hover{
    color: #ffffff;
    -webkit-filter: drop-shadow(0rem 0.25rem 0.2rem #FD7B1C);
            filter: drop-shadow(0rem 0.25rem 0.2rem #FD7B1C);
}
