@media only screen and (min-width:2560px) {

    /* Section 4 */
    .skill-set-head{
        font-size: 8rem;
    }
    .skill-inner{
        font-size: 30px;
        line-height: 2.5rem;
    }
    .skills h2{
        font-size: 40px;
    }
    .skills p{
        font-size:28px ;
        line-height: 2.5rem;
    }
  
  }


  @media only screen and (max-width:426px){
    .skill-set-head{
        position: relative;
        bottom: 9vh;
    }
    .skill-description{
        text-align: start;
        text-indent: 3rem;
    }
  }
    

  
  
.SocialMedia{
    max-width: 100%;
}
.social-content{
    background-color: black;
}
.social-content h2{
    font-size: 1.5rem;
    font-weight: 700;
    text-align: center;
    color: #EBA371;
    position: relative;
    bottom: 6vh;
    letter-spacing: 5PX;
}
.social-content h3{
    font-size: 3.5rem;
    font-weight: 700;
    text-align: center;
    color: #f6caad09;
}
.social-content p{
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
    color: white;
    text-align: center;
}
@media only screen and (max-width:426px){
    .social-content p{
        text-indent: 35px;
        text-align: justify;
        font-size: 15px;
    }
  
}


