.projects-description{
    background-color: #1818232f;
    border-radius: 0 0 20px 20px;
    text-align: center;
}

.animation-project{
    animation: side 1s ease-in-out infinite alternate-reverse both;
}
.animation-project:hover{
    filter: drop-shadow(0rem -0.2rem 0.2rem #e4f129);
}

.project-title{
    color: #7F88FD;
    font-weight: 600;
}

.work-content{
    font-size: 18px;
    font-weight: 400;
}
.work-head-shadow{
    font-size: 3.5rem;
    font-weight: 700;
    color: #7f8c9a0c;
}
.word-head{
    color: #68727F;
    position: relative;
    bottom: 6vh;
    font-weight: 700;
}

.custom-work-button{
    height: 50vh;
    
}
.button-stytle{
    border: 1px solid #5221E6;
    transition: 0.3s;
}
.button-stytle:hover{
    background-color:#5221E6;
    box-shadow: 0 0 20px #5221E6;
}
.work-1, .work-2, .work-3, .work-4{
    max-width: 100%;
    border-radius: 20px 20px 0 0 ;
}
.demo{
    transition: 0.8s;
    border-radius: 10px;
}
.demo:hover{
    box-shadow: 0 0 20px #0D6EFD;
}
  /* Tablet and mobile */
  @media only screen and (max-width:426px)  {

    .work-content{
        text-align: justify;
        text-indent: 35px;
    }
   
  }
  
