.work-title{
    font-size: 1.5rem;
    font-weight: 700;
    text-align: center;
    position: relative;
    bottom: 8vh;
    word-spacing: 20px;
    letter-spacing: 5px;
}
.head-shadow{
    font-size: 3.5rem;
    font-weight: 700;
    text-align: center;
    color: rgba(255, 255, 255, 0.034);
}
.section-2 div:nth-child(1){
    background-color: black;
}
.num-head{
    font-size: 7rem;
    line-height: 7rem;
}
.company-head{
    font-size: 24px;
    font-weight: 700;
}

.work-detail{
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5rem;
}
.work-description{
  font-size: 3.5rem;
    font-weight: 700;
    line-height: 3.5rem;
    text-align: center;
}
.inner-section{
 text-align: -webkit-center;
}
.work1, .work2, .work3, .work4{
    text-decoration: none;
    transition: 0.5s;
}
.work1{
    color: #3CC74E;
}
.work2{
    color:#578FF7;
}
.work3{
    color: #E95D90;
}
.work1:hover{
    color: #3CC74E;
    filter: drop-shadow(0rem 0.2rem 0.2rem #3cc74f85);
}
.work2:hover{
    color:#578FF7;
    filter: drop-shadow(0rem 0.2rem 0.2rem #578ff78e);
}
.work3:hover{
    color: #E95D90;
    filter: drop-shadow(0rem 0.2rem 0.2rem #e95d9081);
}

@media only screen and (min-width:2560px) {
    /* Section 2 */
    .work-title{
        font-size: 5rem;
    }
    .num-head{
        font-size: 9rem;
    }
    .work-detail{
        font-size: 2rem;
        line-height: 2.5rem;
    }
  
  }
    

  
  /* Mini Laptop Screen */
  @media only screen and (max-width:1024px)  {
  
    .work-title{
        font-size: 25px;
    }
    .num-head{
        font-size: 5rem;
    }
    .work-detail{
        font-size: 16px;
        line-height: 1.5rem;
    }
    .work-description{
        font-size: 2rem;
        line-height: 2.5rem;
  
    }
  }
  
  /* Tablet and mobile */
  @media only screen and (max-width:768px)  {
  
    .work-title{
        /* font-size: 16px; */
        position: relative;
        bottom: 8vh;
    }
    .head-shadow{
        font-size: 3rem;
    }
    .num-head{
        font-size: 6rem;
    }
    .work-detail{
        font-size: 18px;
        line-height: 1.8rem;
    }
    .work-description{
        font-size: 2.5rem;
        line-height: 3rem;
  
    }
   
  }
  
 