@import url('https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap');
.article-content{
    background-color: black;
    text-align: center;
}
.content h2{
    font-size: 1.5rem;
    font-weight:700 ;
    text-align: center;
    color: #cedf65;
    position: relative;
    bottom: 6vh;
    letter-spacing: 5px;
}
.article-button{
    font-weight: 400;
    transition: 0.8s;
    border-radius: 20px;
}
hr{
    color: beige;
}
.article-button:hover{
    border: 1px solid #597B11;
    background-color: #597B11;
    border-radius: 20px;
    color: rgb(255, 255, 255);    
}
#philosophy-shadow{
    color: #cfdf650a;
    font-size: 3.5rem;
    font-weight:700 ;
    text-align: center;
}

.article-content p{
    font-size: 20px;
    font-weight: 400;
    color: white;
    
}
.article-image{
    max-width: 100%;
}

/* MOdal design */


.modal-pic{
    max-width: 100%;
    border-radius: 100%;
    box-shadow: 0 0 15px grey;

}
.modal-content{
    text-align: center;
}
.modal-content h2{
    font-family: 'Architects Daughter', cursive;
    background: #6629CF;
    background: -webkit-linear-gradient(to right, #6629CF 31%, #FF1FC3 65%, #AB1DCF 72%);
    background: -moz-linear-gradient(to right, #6629CF 31%, #FF1FC3 65%, #AB1DCF 72%);
    background: linear-gradient(to right, #6629CF 31%, #FF1FC3 65%, #AB1DCF 72%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.modal-content{
    background-color: rgba(0, 0, 0, 0.897);
}

.modal-content  h3{
    font-weight: 700;
    font-size: 15px;
    color: rgb(152, 106, 158);
}
.modal-content p{
    font-family: 'Balsamiq Sans', cursive;
    font-weight: 400;
    color: rgba(255, 255, 255, 0.808);
    line-height: 1.8rem;
    font-size: 16px;
    word-spacing: 2px;
}
.modal-footers{
    text-align: center;
}
.modal-media a i{
   color: grey;
}
.modal-close{
    transition: 0.5s;
}
.modal-close:hover{
    color: white;
    cursor: pointer;
}

@media only screen and (min-width:2560px) {
    
    .content h2{
        font-size: 6.5rem;
    }
    .content p{
        font-size: 2rem;
    }
    .article-button{
        font-size: 2rem;
    }
  
  }
    
  /* Smaller screen Mobile */
  @media only screen and (max-width:426px){

    .article-button{
        font-size: 1rem;
    }
    #philosophy-shadow{
        font-size: 3rem;
    }
    .content h2{
        font-size: 1.5rem;
    }
    .content p{
        font-size: 18px;
        text-indent: 40px;
        text-align: justify;
    }
  
   
  }