@import url('https://fonts.googleapis.com/css2?family=Sen&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  font-family: 'Sen', sans-serif;
  overflow-x: hidden; 
  background-color: black; 
}
::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 20px; 
  }
  ::-webkit-scrollbar-thumb{
    background: #341F3A; 
    border-radius: 20px;
    transition: 0.8s;
    height: 5vh;
  }
  ::-webkit-scrollbar-thumb:hover {
    background: #181823; 
  }
  ::-webkit-scrollbar {
    width: 8px;
  }
.lite-gray-color{
  color: #8491A0;
}
.bg-black{
  background-color: black;
}

/* Bigger screen  */
@media only screen and (min-width:2560px) {

  /* Section 1 */
 
  .hero-details{
      position: relative;
      left: -5vw;
  }
  .hero-details h1{
      font-size: 86px;
  }
  .hero-details h1 strong{
      font-size: 86px;
  }
  .hero-detail{
      font-size: 26px;
  }
  /* Section 2 */
  .work-title{
      font-size: 5rem;
  }
  .num-head{
      font-size: 9rem;
  }
  .work-detail{
      font-size: 2rem;
      line-height: 2.5rem;
  }
  /* Section 3 */
  
  .content h2{
      font-size: 6.5rem;
  }
  .content p{
      font-size: 2rem;
  }
  .article-button{
      font-size: 2rem;
  }

  /* Section 4 */
  .skill-set-head{
      font-size: 8rem;
  }
  .skill-inner{
      font-size: 30px;
      line-height: 2.5rem;
  }
  .skills h2{
      font-size: 40px;
  }
  .skills p{
      font-size:28px ;
      line-height: 2.5rem;
  }

}
  
/* Laptop screen */
@media only screen and (max-width:1440px)  {
  .hero-details{
      position: relative;
      left: -7vw;
  }
  .hero-details  h1{
      font-size: 70px;
  }
  .hero-details h1 strong{
      font-size: 70px;
  }
  .hero-detail{
      font-size: 16px;
  }
}

/* Mini Laptop Screen */
@media only screen and (max-width:1024px)  {
  .hero-details h1{
      font-size: 48px;
  }
  .hero-details   h1 strong{
      font-size: 48px;
  }
  .hero-detail{
      font-size: 12px;
  }

  .work-title{
      font-size: 12px;
  }
  .num-head{
      font-size: 5rem;
  }
  .work-detail{
      font-size: 16px;
      line-height: 1.5rem;
  }
  .work-description{
      font-size: 2rem;
      line-height: 2.5rem;

  }
}

/* Tablet and mobile */
@media only screen and (max-width:768px)  {
  .intro-section{
      padding: 20px;
  }
  .hero-details{
      position: relative;
      left: 0;
  }
  .hero-details h1{
      padding-top: 2vh;
      font-size: 40px;
  }
 .hero-details h1 strong{
      font-size: 40px;
  }
  .hero-detail{
      font-size: 15px;
      padding-top: 1vh;
  }

  .work-title{
      font-size: 16px;
  }
  .num-head{
      font-size: 6rem;
  }
  .work-detail{
      font-size: 18px;
      line-height: 1.8rem;
  }
  .work-description{
      font-size: 2.5rem;
      line-height: 3rem;

  }
 
}

/* Smaller screen Mobile */
@media only screen and (max-width:425px){
   /* Section 3 */
  
  .content h2{
      padding-top: 2vh;
      font-size: 1.8rem;
  }
  .content p{
      font-size: 14px;
  }
  .article-button{
      font-size: 1rem;
  }
  .contact-head{
      font-size: 20px;
  }

 
}
