.footer-section{
    background-color: #181823;
}
.footer-link{
    text-align: center;
    font-size: 15px;
    letter-spacing: 2px;
    font-weight: 100;
}



.footer-link a {
    text-decoration: none;
    color: white;
    transition: 0.8s;
    animation: spins 20s ease-in; 
    
    
}
.footer-link a:hover{
    color: rgba(128, 128, 128, 0.432);
      
}
@keyframes spins { 
    from { 
        transform: scale(1.2,1.2); 
    } to { 
        transform: scale(1.2,1.2); 
    }
}
.footer-link li{
    list-style: none;
}
.transform{
    transition: 0.8s;
}
.transform:hover{
    animation: spin 10s ease-in;
    
}
.foot a i{
    color: rgba(128, 128, 128, 0.705);
    transition: 1s;
}
 .whatsApp:hover{
    color: rgb(93, 199, 83);
    filter: drop-shadow(0rem 0.1rem 0.1rem rgb(93, 199, 83));
    animation: spin 20s ease-in;
}

.instagram:hover{
    color: #E36638;
    filter: drop-shadow(0rem 0.1rem 0.1rem #E36638);
    animation: spin 20s ease-in;
}
.linkedIn:hover{
    color: #1970C6;
    filter: drop-shadow(0rem 0.1rem 0.1rem #196fc67c);
    animation: spin 20s ease-in;
}
.gitHub:hover{
    color: #a156d3;
    filter: drop-shadow(0rem 0.1rem 0.1rem #a156d39f);
    animation: spin 20s ease-in;
}
@keyframes spin { 
    from { 
        transform: scale(1.2,1.2); 
    } to { 
        transform: scale(1.2,1.2); 
    }
}